import React from "react";
import {
	Card,
	Button,
	CardFooter,
	CardHeader,
	CardBody,
	CardDeck,
	CardText,
} from "reactstrap";
// import BGI from "../Images/mesh.png";
import Col from "reactstrap/lib/Col";
import Row from "reactstrap/lib/Row";
import Slider from "react-slick";
import productsArray from "./DummyData/products";

const Offers = (props) => {
	let lgSettings = {
		dots: true,
		infinite: false,
		speed: 500,
		arrows: true,
		slidesToShow: 3,
		slidesToScroll: 3,
	};

	let smSettings = {
		dots: true,
		infinite: false,
		speed: 500,
		arrows: true,
		slidesToShow: 1,
		slidesToScroll: 1,
	};

	return (
		<React.Fragment>
			<div className="py-5" id="deals">
				<p className="display-4 text-center my-5 font-weight-bold">Deals</p>
				{/* will be hidden on screen less than lg */}
				<div className="d-none d-lg-block">
					<Slider {...lgSettings} className="mx-5 text-center py-5">
						{productCards()}
					</Slider>
				</div>
				<div className="d-lg-none">
					{/* will be hidden on screen more than lg */}
					<Slider {...smSettings} className="mx-5 text-center py-5">
						{productCards()}
					</Slider>
				</div>
			</div>
		</React.Fragment>
	);
};

export default Offers;

export const productCards = () => {
	return productsArray.map((item, i) => (
		<div className="out">
			<Card className="mx-2 flip_card border-0">
				<div className="card_front text-white h2 rounded">
					<CardBody>
						<Row>
							<Col>
								<p className="font-weight-bold">
									{item.name} <br />
									<small className="text-muted">anytime</small>
								</p>
							</Col>
							+
							<Col>
								<p className="font-weight-bold">
									{item.name} <br />
									<small className="text-muted">night data</small>
								</p>
							</Col>
						</Row>
						<Row>
							<Col className="h3 text-white font-italic">{item.price}</Col>
						</Row>
					</CardBody>
				</div>
				<div className="card_back text-white rounded h-100">
					<CardBody>
						<p>
							Lorem ipsum dolor sit amet consectetur adipisicing elit. Id porro
							facilis, at mollitia consequatur provident quod nemo earum eius
							facere reiciendis.
						</p>
						<Button color="light" outline className="text-center">
							View
						</Button>
					</CardBody>
				</div>
			</Card>
		</div>
	));
};
